import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, ScreenSpinner, Epic, PanelHeader, Panel, Tabbar, TabbarItem, Avatar, Div, HeaderButton, List, Cell, Snackbar, Button} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Polls from 'react-polls';

const StyleButton = {
  outline: "none",
  width:"100%",
  color: "white",
  fontFamily:"Arial",
  padding: "20px 30px",
  borderRadius: "10px",
  backgroundImage: "linear-gradient(to right, #9EEFE1 0%, #4830F0 51%, #9EEFE1 100%)",
  backgroundSize: "200%",
  boxShadow:" 0 0 20px rgba(0,0,0,.1)",
  transition: ".5s",
  marginTop: "1.5%"
}

const pollQuestion = 'Is react-polls useful?'
const pollAnswers = [
  { option: 'Yes', votes: 8 },
  { option: 'No', votes: 2 }
]
class Poll extends React.Component {

  // Handling user vote
  // Increments the votes count of answer when the user votes
  handleVote = voteAnswer => {
     const { pollAnswers } = this.state
     const newPollAnswers = pollAnswers.map(answer => {
       if (answer.option === voteAnswer) answer.votes++
       return answer
     })
     this.setState({
       pollAnswers: newPollAnswers
     })
   }



  render(){


   return(

    <div>
       <Poll question={pollQuestion} answers={pollAnswers} onVote={this.handleVote} />
     </div>

	 	);
	}
}

export default Poll;
